._2qCPL {
  position: absolute;
  z-index: 1;
  width: 78vw;

  transform-origin: left top;
  transition: .25s all 2s;

}
  
/* * to tablet */
  
@media (min-width: 550px) { 
  ._2qCPL {
    width: 50vw;
  }

  ._2qCPL.vF7L1 {
    transition: .25s all 0s;
    transform: scaleX(1.1) scaleY(1.1);
  }
}
  
/* * to desktop */
  
@media (min-width: 768px) { 
  ._2qCPL {
    width: 30vw;
  }
}
  
/* to desktop */
  
@media (min-width: 1025px) {  
  ._2qCPL {
    width: 30vw;
  }
}