.zsznE {
  display: block;
  position: fixed;
  top: 0;
  width: 35px;
  height: 35px;
  color: var(--font-color-white);
  text-shadow: 0 0 4px black;
  font-size: 25px;
  right: 5%;
  top: 70%;
  z-index: 1;
}

._2vBPE {
  background: rgba(68,65,65,0.83922);
  height: 45px;
  width: 100%;
  position: absolute;
  display: flex;
  justify-content: space-around;
  align-items: center;
  transform: scale(0);
  transform-origin: top left;
  transition: 100ms all;
  z-index: 1;
}

._2vBPE._2eFCT {
  transform: scale(1);
}

._2abAg {
  background: transparent;
  color: white;
  border: none;
  outline-width: 0;
  height: 100%;
  text-decoration: none;
}

.hjKZb {
  font-size: 25px;
  color: var(--font-color-white);
  text-shadow: 0 0 4px black;
}

._3_N7E {
  height: calc(100vh - 45px);
  width: 100%;
  top: 45px;
  position: absolute;
  z-index: 1;
  background: rgba(128,128,128,0.5098);
  transition: opacity 300ms ease-in, visibility 0s ease 0s;
  opacity: 0;
  visibility: hidden;
}

._3_N7E._2eFCT {
  opacity: 1;
  visibility: visible;
}

/* * to tablet */

@media (min-width: 550px) { 
  .zsznE, 
  ._2vBPE, 
  ._3_N7E {
    display: none;
  }
  
}

